'use client';

import { useEffect, useState } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { getStorageStats, getLaudos } from '@/lib/storage';
import { getAllTemplates } from '@/lib/templates';
import { Laudo, TipoLaudo } from '@/types/laudo';

const TIPO_LABELS: Record<TipoLaudo, string> = {
  'acidentes-transito': 'Acidentes de Trânsito',
  'crimes-vida': 'Crimes Contra a Vida',
  'crimes-patrimonio': 'Crimes Contra o Patrimônio',
  'constatacao-drogas': 'Constatação de Drogas',
  'incendios': 'Incêndios',
  'crimes-meio-ambiente': 'Crimes Contra o Meio Ambiente',
  'identificacao-veiculos': 'Identificação de Veículos',
  'fisico-descritivo': 'Físico Descritivo'
};

export default function Dashboard() {
  const [stats, setStats] = useState<{
    totalLaudos: number;
    laudosPorTipo: Record<TipoLaudo, number>;
    ultimoLaudo?: Date;
  }>({ totalLaudos: 0, laudosPorTipo: {} as Record<TipoLaudo, number> });
  
  const [recentLaudos, setRecentLaudos] = useState<Laudo[]>([]);
  const templates = getAllTemplates();

  useEffect(() => {
    const storageStats = getStorageStats();
    setStats(storageStats);
    
    const laudos = getLaudos();
    const recent = laudos
      .sort((a, b) => new Date(b.updatedAt).getTime() - new Date(a.updatedAt).getTime())
      .slice(0, 5);
    setRecentLaudos(recent);
  }, []);

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Dashboard</h1>
          <p className="text-gray-600 mt-1">
            Gerencie seus laudos periciais de forma profissional
          </p>
        </div>
        <Button 
          onClick={() => window.location.href = '/novo-laudo'}
          className="bg-blue-600 hover:bg-blue-700"
        >
          Criar Novo Laudo
        </Button>
      </div>

      {/* Estatísticas */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total de Laudos</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.totalLaudos}</div>
            <p className="text-xs text-gray-600">
              {stats.ultimoLaudo 
                ? `Último: ${stats.ultimoLaudo.toLocaleDateString('pt-BR')}`
                : 'Nenhum laudo criado ainda'
              }
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Tipos Disponíveis</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{templates.length}</div>
            <p className="text-xs text-gray-600">
              Templates especializados
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Mais Utilizado</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-sm font-bold">
              {Object.keys(stats.laudosPorTipo).length > 0 
                ? TIPO_LABELS[Object.entries(stats.laudosPorTipo)
                    .sort(([,a], [,b]) => b - a)[0][0] as TipoLaudo]
                : 'N/A'
              }
            </div>
            <p className="text-xs text-gray-600">
              {Object.keys(stats.laudosPorTipo).length > 0 
                ? `${Math.max(...Object.values(stats.laudosPorTipo))} laudos`
                : 'Nenhum dado disponível'
              }
            </p>
          </CardContent>
        </Card>
      </div>

      {/* Templates Disponíveis */}
      <Card>
        <CardHeader>
          <CardTitle>Templates Disponíveis</CardTitle>
          <CardDescription>
            Escolha o tipo de laudo pericial que deseja criar
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {templates.map((template) => (
              <div
                key={template.tipo}
                className="border rounded-lg p-4 hover:shadow-md transition-shadow cursor-pointer"
                onClick={() => window.location.href = `/novo-laudo?tipo=${template.tipo}`}
              >
                <div className="flex justify-between items-start mb-2">
                  <h3 className="font-semibold text-sm">{template.nome}</h3>
                  <Badge variant="secondary" className="text-xs">
                    {stats.laudosPorTipo[template.tipo] || 0}
                  </Badge>
                </div>
                <p className="text-xs text-gray-600 mb-3">
                  {template.descricao}
                </p>
                <Button 
                  size="sm" 
                  variant="outline" 
                  className="w-full text-xs"
                  onClick={(e) => {
                    e.stopPropagation();
                    window.location.href = `/novo-laudo?tipo=${template.tipo}`;
                  }}
                >
                  Criar Laudo
                </Button>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>

      {/* Laudos Recentes */}
      {recentLaudos.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle>Laudos Recentes</CardTitle>
            <CardDescription>
              Seus últimos laudos criados ou editados
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-3">
              {recentLaudos.map((laudo) => (
                <div
                  key={laudo.id}
                  className="flex items-center justify-between p-3 border rounded-lg hover:bg-gray-50 cursor-pointer"
                  onClick={() => window.location.href = `/laudo/${laudo.id}`}
                >
                  <div className="flex-1">
                    <div className="flex items-center gap-2 mb-1">
                      <span className="font-medium text-sm">{laudo.numeroLaudo}</span>
                      <Badge variant="outline" className="text-xs">
                        {TIPO_LABELS[laudo.tipo]}
                      </Badge>
                    </div>
                    <p className="text-sm text-gray-600 truncate">
                      {laudo.titulo}
                    </p>
                    <p className="text-xs text-gray-500">
                      Atualizado em {new Date(laudo.updatedAt).toLocaleDateString('pt-BR')}
                    </p>
                  </div>
                  <Button 
                    size="sm" 
                    variant="ghost"
                    onClick={(e) => {
                      e.stopPropagation();
                      window.location.href = `/laudo/${laudo.id}`;
                    }}
                  >
                    Ver
                  </Button>
                </div>
              ))}
            </div>
            <div className="mt-4 text-center">
              <Button 
                variant="outline" 
                onClick={() => window.location.href = '/meus-laudos'}
              >
                Ver Todos os Laudos
              </Button>
            </div>
          </CardContent>
        </Card>
      )}

      {/* Guia de Início Rápido */}
      {stats.totalLaudos === 0 && (
        <Card>
          <CardHeader>
            <CardTitle>Bem-vindo ao Gerador de Laudos Periciais!</CardTitle>
            <CardDescription>
              Comece criando seu primeiro laudo pericial
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div className="flex items-start space-x-3">
                <div className="flex-shrink-0 w-6 h-6 bg-blue-100 rounded-full flex items-center justify-center">
                  <span className="text-sm font-medium text-blue-600">1</span>
                </div>
                <div>
                  <h4 className="font-medium">Escolha o tipo de laudo</h4>
                  <p className="text-sm text-gray-600">
                    Selecione entre 8 tipos especializados de laudos periciais
                  </p>
                </div>
              </div>
              <div className="flex items-start space-x-3">
                <div className="flex-shrink-0 w-6 h-6 bg-blue-100 rounded-full flex items-center justify-center">
                  <span className="text-sm font-medium text-blue-600">2</span>
                </div>
                <div>
                  <h4 className="font-medium">Preencha os dados</h4>
                  <p className="text-sm text-gray-600">
                    Complete o formulário com as informações da perícia
                  </p>
                </div>
              </div>
              <div className="flex items-start space-x-3">
                <div className="flex-shrink-0 w-6 h-6 bg-blue-100 rounded-full flex items-center justify-center">
                  <span className="text-sm font-medium text-blue-600">3</span>
                </div>
                <div>
                  <h4 className="font-medium">Gere o PDF</h4>
                  <p className="text-sm text-gray-600">
                    Visualize e exporte seu laudo em formato PDF profissional
                  </p>
                </div>
              </div>
              <div className="pt-4">
                <Button 
                  onClick={() => window.location.href = '/novo-laudo'}
                  className="bg-blue-600 hover:bg-blue-700"
                >
                  Criar Meu Primeiro Laudo
                </Button>
              </div>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
