import { Laudo, TipoLaudo } from '@/types/laudo';

const STORAGE_KEY = 'laudos-periciais';
const SETTINGS_KEY = 'laudos-settings';

export interface LaudoSettings {
  peritoPadrao: string;
  numeroSequencial: number;
  prefixoLaudo: string;
}

export function saveLaudo(laudo: Laudo): void {
  const laudos = getLaudos();
  const existingIndex = laudos.findIndex(l => l.id === laudo.id);
  
  if (existingIndex >= 0) {
    laudos[existingIndex] = laudo;
  } else {
    laudos.push(laudo);
  }
  
  localStorage.setItem(STORAGE_KEY, JSON.stringify(laudos));
}

export function getLaudos(): Laudo[] {
  try {
    const stored = localStorage.getItem(STORAGE_KEY);
    return stored ? JSON.parse(stored) : [];
  } catch (error) {
    console.error('Erro ao carregar laudos:', error);
    return [];
  }
}

export function getLaudo(id: string): Laudo | null {
  const laudos = getLaudos();
  return laudos.find(l => l.id === id) || null;
}

export function deleteLaudo(id: string): void {
  const laudos = getLaudos();
  const filtered = laudos.filter(l => l.id !== id);
  localStorage.setItem(STORAGE_KEY, JSON.stringify(filtered));
}

export function getLaudosByTipo(tipo: TipoLaudo): Laudo[] {
  const laudos = getLaudos();
  return laudos.filter(l => l.tipo === tipo);
}

export function getSettings(): LaudoSettings {
  try {
    const stored = localStorage.getItem(SETTINGS_KEY);
    return stored ? JSON.parse(stored) : {
      peritoPadrao: '',
      numeroSequencial: 1,
      prefixoLaudo: 'LP'
    };
  } catch (error) {
    console.error('Erro ao carregar configurações:', error);
    return {
      peritoPadrao: '',
      numeroSequencial: 1,
      prefixoLaudo: 'LP'
    };
  }
}

export function saveSettings(settings: LaudoSettings): void {
  localStorage.setItem(SETTINGS_KEY, JSON.stringify(settings));
}

export function generateLaudoNumber(): string {
  const settings = getSettings();
  const numero = `${settings.prefixoLaudo}-${String(settings.numeroSequencial).padStart(4, '0')}`;
  
  // Incrementar o número sequencial
  settings.numeroSequencial += 1;
  saveSettings(settings);
  
  return numero;
}

export function generateLaudoId(): string {
  return `laudo-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
}

export function exportLaudos(): string {
  const laudos = getLaudos();
  const settings = getSettings();
  
  return JSON.stringify({
    laudos,
    settings,
    exportDate: new Date().toISOString(),
    version: '1.0'
  }, null, 2);
}

export function importLaudos(jsonData: string): { success: boolean; message: string } {
  try {
    const data = JSON.parse(jsonData);
    
    if (!data.laudos || !Array.isArray(data.laudos)) {
      return { success: false, message: 'Formato de arquivo inválido' };
    }
    
    // Fazer backup dos dados atuais
    const currentLaudos = getLaudos();
    const currentSettings = getSettings();
    
    try {
      // Importar laudos
      localStorage.setItem(STORAGE_KEY, JSON.stringify(data.laudos));
      
      // Importar configurações se existirem
      if (data.settings) {
        localStorage.setItem(SETTINGS_KEY, JSON.stringify(data.settings));
      }
      
      return { 
        success: true, 
        message: `${data.laudos.length} laudos importados com sucesso` 
      };
    } catch (error) {
      // Restaurar backup em caso de erro
      localStorage.setItem(STORAGE_KEY, JSON.stringify(currentLaudos));
      localStorage.setItem(SETTINGS_KEY, JSON.stringify(currentSettings));
      throw error;
    }
  } catch (error) {
    console.error('Erro ao importar laudos:', error);
    return { 
      success: false, 
      message: 'Erro ao processar arquivo de importação' 
    };
  }
}

export function clearAllData(): void {
  localStorage.removeItem(STORAGE_KEY);
  localStorage.removeItem(SETTINGS_KEY);
}

export function getStorageStats(): { 
  totalLaudos: number; 
  laudosPorTipo: Record<TipoLaudo, number>;
  ultimoLaudo?: Date;
} {
  const laudos = getLaudos();
  
  const laudosPorTipo = laudos.reduce((acc, laudo) => {
    acc[laudo.tipo] = (acc[laudo.tipo] || 0) + 1;
    return acc;
  }, {} as Record<TipoLaudo, number>);
  
  const ultimoLaudo = laudos.length > 0 
    ? new Date(Math.max(...laudos.map(l => new Date(l.updatedAt).getTime())))
    : undefined;
  
  return {
    totalLaudos: laudos.length,
    laudosPorTipo,
    ultimoLaudo
  };
}
